export class BMEKind {
    static PRESSURE = new BMEKind("pressure", "field2");
    static TEMPERATURE = new BMEKind("temperature", "field1");
    static HUMIDITY = new BMEKind("humidity", "field3");
    constructor(name, apiKey) {
      this.name = name;
      this.apiKey = apiKey;
    }
  }
  export class VictronKind {
    static VOLTAGE = new VictronKind("voltage", "field1");
    static CURRENT = new VictronKind("current", "field2");
    static POWER = new VictronKind("power", "field5");
    static TEMPERATURE = new VictronKind("temperature", "field8");
    constructor(name, apiKey) {
      this.name = name;
      this.apiKey = apiKey;
    }
  }

  