import { cloneDeep } from 'lodash'
import { format, parseISO } from "date-fns";
import sv from "date-fns/locale/sv";

export const chartData = {
    type: 'line',
    data: {
        datasets: [{
            label: 'Temperature',
            data: [15, 20, 40, 41],
            fill: false
        }],
        labels: ['January', '',  'March', 'April']
    },
    options: {
        maintainAspectRatio: false,
        responsive: true,
        legend:{ display: false },
        elements: { point:{ radius: 0 } },  
        scales: {
            xAxes: [{
                type: 'time',
                time:{
                    unit:'hour',
                    displayFormats: {
                        hour: 'HH:mm'
                    }
                },
                scaleLabel: {
                  display: true,
                  labelString: 'tiden'
                },
              }]
        }
    }
};



export function getChartData(rawdata){
    let dta = cloneDeep(chartData),
        dtap = rawdata.map( t => (t.value ?? 0 ) / 1000),
        lbl2 = rawdata.map( t => format(parseISO(t.dt), "HH:mm", {locale: sv})),
        lbl = rawdata.map( t => t.dt);  
    console.log(lbl2);      
    dta.data = { 
        datasets: [{
            label: 'Voltage',
            data: dtap,
            fill: false
            }],
        labels: lbl
    };
    dta.options.scales.x = { title: { display: true, text: 'Month',color: '#911'}};
    return dta;
}

