<template>
  <v-container>
      <h3>
      <v-menu>
          <template v-slot:activator="{ on, attrs }">
                <v-btn x-small  v-bind="attrs" v-on="on">
                      keyboardvariants
                    </v-btn>
                </template>
                <v-list >
                  <v-list-item-group v-model="variant">
                    <v-list-item v-for="v in opts" :key="v.id">
                       <v-list-item-action>
                            <v-list-item-title>{{v.type}}-{{v.inputmode}} </v-list-item-title>
                        </v-list-item-action>
                    </v-list-item>
                  </v-list-item-group>
                  </v-list>
            </v-menu>
       Input keyboard test</h3>     
       <v-divider/>
       Using vue databinding
       <v-row >
       <v-col cols="6"> {{type}}-{{inputmode}} </v-col>
       <v-col cols="3"><input :type="type" :inputmode="inputmode" v-model="val" style="background:aliceblue"/></v-col>
       <v-col cols="3" > == {{ val }}</v-col>
       </v-row>
       <v-divider/>
       Using input events
       <v-row >
       <v-col cols="6"> {{type}}-{{inputmode}} </v-col>
       <v-col cols="3">
         <input :type="type" :inputmode="inputmode" style="background:aliceblue"
          @input="handleInput($event)" /></v-col>
       <v-col cols="3" > == {{inputVal}} </v-col>
       </v-row>
       <v-divider/>

  </v-container>
</template>

<script>
  export default {
    name: 'About',
    data: () => ({
      val:null,
      variant:0,
      opts:[
        {id:0, type:' ',inputmode:' '},
        {id:1, type:'text',inputmode:'numeric'},
        {id:2, type:'text',inputmode:'decimal'},
        {id:3, type:'number',inputmode:'numeric'},
        {id:4, type:'number',inputmode:'decimal'},
      ],
      inputVal:null
     }),
     methods: {
       handleInput(x){
         this.inputVal = x.target.value;
       }
     },
     computed:{
       type(){
         return this.opts[this.variant]?.type;
       },
       inputmode(){
         return this.opts[this.variant]?.inputmode;
       }
     },
  }
</script>
