<template> 
  <v-container pa-4>
    <v-row no-gutters>
      <v-col :cols="cols">
        <v-card  class="pb-4"> 
          <v-card-title> Solar array status </v-card-title>
        <Chart :chartData="getChartData(victronKind.VOLTAGE)" :height="h"/>
        </v-card>
      </v-col>
      <v-col :cols="cols">
        <v-card > 
          <v-card-title> Voltage </v-card-title>
        <Chart :chartData="getChartData(victronKind.VOLTAGE)" :height="h"/>
        </v-card>
      </v-col>
    </v-row>
   </v-container>
 </template>

<script>
import Chart from '../components/Chart.vue'
import {getChartData, getChartData2} from './solar.js'
import { mapGetters } from 'vuex';

import {VictronKind} from '../common/common.js'
  export default {
    name: 'Solar',
    data: () => ({ 
      victronKind: VictronKind,
    }),
    methods:{
      getChartData(kind){
        let data = data = this.getVictronHist(kind);
          getChartData2(this.getVictronPayload);
        return getChartData(data) || {};
      }
    },
   computed: {
      ...mapGetters([
        'getVictronCurrent',
        'getVictronHist',
        'getVictronPayload',
      ]),
      cols(){return this.$vuetify.breakpoint.smAndUp ? 6 : 12},
      h(){return this.$vuetify.breakpoint.smAndUp ? '75vh' : '35vh'},
  },
  components: {
   Chart
    },
  }
</script>
